import React from "react";
import { observer } from "mobx-react";
import { Link, useNavigate } from "react-router-dom";
import { Formik, Form } from 'formik';
import * as Yup from 'yup';

import { Button } from "src/shared/components/Button";
import { TextInput } from "src/shared/components/TextInput";
import SvgLogo from "src/assets/Logo";

import styles from "./FormLogin.module.css";

export const FormLogin = observer(()  => {
  let navigate = useNavigate();

  const login = (event: Event) => {
    event.preventDefault();
    navigate("/");
  }

  return (
    <div className={styles['FormLogin']}>
      <SvgLogo />
      <Formik
        initialValues={{ login: "", password: "" }}
        onSubmit={(values, { setSubmitting }) => {
          console.log(values);
        }}
        validationSchema={Yup.object({
          login: Yup.string()
            .required(' '),
          password: Yup.string()
            .required(' '),
        })}
      >
        {props => (
          <Form className={styles['FormLogin-Form']}>
            <TextInput name="login" type="text" className={styles['FormLogin-Input']} placeholder="" />
            <TextInput name="password" type="text" className={styles['FormLogin-Input']} placeholder=""/>
            <Button className={styles['FormLogin-ButtonLogin']} type="submit" onClick={login}>
              <span></span>
            </Button>
          </Form>
        )}
      </Formik>
      <Link to="../register" className={styles['FormLogin-Register']}><span></span></Link>
    </div>
  )
});
